  export default "import React from 'react';\n\nimport SemanticPreview from '../../../.dumi/components/SemanticPreview';\nimport useLocale from '../../../.dumi/hooks/useLocale';\n\nimport { Avatar, List, Space } from 'antd';\nimport { LikeOutlined, MessageOutlined, StarOutlined } from '@ant-design/icons';\n\nconst locales = {\n  cn: {\n    extra: '设置额外内容',\n    actions: '设置列表操作组',\n  },\n  en: {\n    extra: 'set `extra` of List.Item',\n    actions: 'set `actions` of List.Item',\n  },\n};\n\nconst IconText = ({ icon, text }: { icon: React.FC; text: string }) => (\n  <Space>\n    {React.createElement(icon)}\n    {text}\n  </Space>\n);\n\nconst data = Array.from({ length: 1 }).map((_, i) => ({\n  href: 'https://ant.design',\n  title: `ant design part ${i}`,\n  avatar: `https://api.dicebear.com/7.x/miniavs/svg?seed=${i}`,\n  description:\n    'Ant Design, a design language for background applications, is refined by Ant UED Team.',\n  content:\n    'We supply a series of design principles, practical patterns and high quality design resources (Sketch and Axure), to help people create their product prototypes beautifully and efficiently.',\n}));\n\nconst BlockList: React.FC<React.PropsWithChildren> = (props) => {\n  const divRef = React.useRef<HTMLDivElement>(null);\n\n  return (\n    <div ref={divRef} style={{ position: 'absolute', inset: 0, height: 300 }}>\n      <List\n        itemLayout=\"vertical\"\n        size=\"large\"\n        dataSource={data}\n        renderItem={(item) => (\n          <List.Item\n            {...props}\n            key={item.title}\n            actions={[\n              <IconText icon={StarOutlined} text=\"156\" key=\"list-vertical-star-o\" />,\n              <IconText icon={LikeOutlined} text=\"156\" key=\"list-vertical-like-o\" />,\n              <IconText icon={MessageOutlined} text=\"2\" key=\"list-vertical-message\" />,\n            ]}\n            extra={\n              <img\n                width={272}\n                alt=\"logo\"\n                src=\"https://gw.alipayobjects.com/zos/rmsportal/mqaQswcyDLcXyDKnZfES.png\"\n              />\n            }\n          >\n            <List.Item.Meta\n              avatar={<Avatar src={item.avatar} />}\n              title={<a href={item.href}>{item.title}</a>}\n              description={item.description}\n            />\n            {item.content}\n          </List.Item>\n        )}\n      />\n    </div>\n  );\n};\n\nconst App: React.FC = () => {\n  const [locale] = useLocale(locales);\n  return (\n    <SemanticPreview\n      height={300}\n      semantics={[\n        { name: 'extra', desc: locale.extra, version: '5.18.0' },\n        { name: 'actions', desc: locale.actions, version: '5.18.0' },\n      ]}\n    >\n      <BlockList />\n    </SemanticPreview>\n  );\n};\n\nexport default App;\n";;
  