  export default "import React from 'react';\nimport { SearchOutlined } from '@ant-design/icons';\nimport { AutoComplete, Button, Form, Input, TreeSelect } from 'antd';\n\nconst formItemLayout = {\n  labelCol: {\n    xs: { span: 24 },\n    sm: { span: 8 },\n  },\n  wrapperCol: {\n    xs: { span: 24 },\n    sm: { span: 16 },\n  },\n};\n\nconst App: React.FC = () => (\n  <Form style={{ margin: '0 auto' }} {...formItemLayout}>\n    <Form.Item label=\"单独 AutoComplete\">\n      <AutoComplete />\n    </Form.Item>\n    <Form.Item label=\"单独 TreeSelect\">\n      <TreeSelect />\n    </Form.Item>\n    <Form.Item label=\"添加 Input.Group 正常\">\n      <Input.Group compact>\n        <TreeSelect style={{ width: '30%' }} />\n        <AutoComplete />\n      </Input.Group>\n    </Form.Item>\n    <Form.Item label=\"包含 search 图标正常\">\n      <AutoComplete>\n        <Input suffix={<SearchOutlined />} />\n      </AutoComplete>\n    </Form.Item>\n    <Form.Item label=\"同时有 Input.Group 和图标发生移位\">\n      <Input.Group compact>\n        <TreeSelect style={{ width: '30%' }} />\n        <AutoComplete>\n          <Input suffix={<SearchOutlined />} />\n        </AutoComplete>\n      </Input.Group>\n    </Form.Item>\n    <Form.Item label=\"同时有 Input.Group 和 Search 组件发生移位\">\n      <Input.Group compact>\n        <TreeSelect style={{ width: '30%' }} />\n        <AutoComplete>\n          <Input.Search />\n        </AutoComplete>\n      </Input.Group>\n    </Form.Item>\n    <Form.Item label=\"Input Group 和 Button 结合\">\n      <Input.Group compact>\n        <TreeSelect style={{ width: '20%' }} />\n        <AutoComplete>\n          <Input.Search />\n        </AutoComplete>\n        <Button type=\"primary\" icon={<SearchOutlined />}>\n          Search\n        </Button>\n      </Input.Group>\n    </Form.Item>\n  </Form>\n);\n\nexport default App;\n";;
  