
  import '/home/runner/work/ant-design/ant-design/components/popover/demo/arrow-point-at-center.tsx?watch=parent';
  export default "import React from 'react';\nimport { createStyles } from 'antd-style';\nimport { Flex, Popover } from 'antd';\nimport type { GetProp } from 'antd';\n\nconst useStyle = createStyles(() => ({\n  item: {\n    width: '280px',\n    height: '280px',\n    display: 'inline-flex',\n    justifyContent: 'center',\n    alignItems: 'center',\n    border: '1px dashed purple',\n  },\n\n  box: {\n    width: '40px',\n    height: '40px',\n    backgroundColor: 'deepskyblue',\n  },\n\n  cross: {\n    position: 'relative',\n\n    '&::before, &::after': {\n      content: '\"\"',\n      position: 'absolute',\n      inset: 0,\n    },\n    '&::before': {\n      top: '50%',\n      height: '1px',\n      backgroundColor: 'red',\n    },\n    '&::after': {\n      left: '50%',\n      width: '1px',\n      backgroundColor: 'blue',\n    },\n  },\n}));\n\ntype Placement = GetProp<typeof Popover, 'placement'>;\n\nconst placements: Placement[] = [\n  'topLeft',\n  'top',\n  'topRight',\n  'leftTop',\n  'left',\n  'leftBottom',\n  'rightTop',\n  'right',\n  'rightBottom',\n  'bottomLeft',\n  'bottom',\n  'bottomRight',\n];\n\nconst App = () => {\n  const { styles, cx } = useStyle();\n  return (\n    <Flex gap={16} wrap>\n      {placements.map((placement) => (\n        <div key={placement} className={styles.item}>\n          <Popover\n            placement={placement}\n            content={\n              <Flex align=\"center\" justify=\"center\">\n                {placement}\n              </Flex>\n            }\n            autoAdjustOverflow={false}\n            arrow={{ pointAtCenter: true }}\n            forceRender\n            open\n          >\n            <div className={cx(styles.box, styles.cross)} />\n          </Popover>\n        </div>\n      ))}\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  