  export default "import React from 'react';\nimport { Button, ConfigProvider, Flex, Popconfirm } from 'antd';\n\nconst text = 'Are you sure to delete this task?';\nconst description = 'Delete the task';\nconst buttonWidth = 80;\n\nconst App: React.FC = () => (\n  <ConfigProvider button={{ style: { width: buttonWidth, margin: 4 } }}>\n    <Flex vertical justify=\"center\" align=\"center\" className=\"demo\">\n      <Flex justify=\"center\" align=\"center\" style={{ whiteSpace: 'nowrap' }}>\n        <Popconfirm\n          placement=\"topLeft\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>TL</Button>\n        </Popconfirm>\n        <Popconfirm\n          placement=\"top\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>Top</Button>\n        </Popconfirm>\n        <Popconfirm\n          placement=\"topRight\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>TR</Button>\n        </Popconfirm>\n      </Flex>\n      <Flex style={{ width: buttonWidth * 5 + 32 }} justify=\"space-between\" align=\"center\">\n        <Flex align=\"center\" vertical>\n          <Popconfirm\n            placement=\"leftTop\"\n            title={text}\n            description={description}\n            okText=\"Yes\"\n            cancelText=\"No\"\n          >\n            <Button>LT</Button>\n          </Popconfirm>\n          <Popconfirm\n            placement=\"left\"\n            title={text}\n            description={description}\n            okText=\"Yes\"\n            cancelText=\"No\"\n          >\n            <Button>Left</Button>\n          </Popconfirm>\n          <Popconfirm\n            placement=\"leftBottom\"\n            title={text}\n            description={description}\n            okText=\"Yes\"\n            cancelText=\"No\"\n          >\n            <Button>LB</Button>\n          </Popconfirm>\n        </Flex>\n        <Flex align=\"center\" vertical>\n          <Popconfirm\n            placement=\"rightTop\"\n            title={text}\n            description={description}\n            okText=\"Yes\"\n            cancelText=\"No\"\n          >\n            <Button>RT</Button>\n          </Popconfirm>\n          <Popconfirm\n            placement=\"right\"\n            title={text}\n            description={description}\n            okText=\"Yes\"\n            cancelText=\"No\"\n          >\n            <Button>Right</Button>\n          </Popconfirm>\n          <Popconfirm\n            placement=\"rightBottom\"\n            title={text}\n            description={description}\n            okText=\"Yes\"\n            cancelText=\"No\"\n          >\n            <Button>RB</Button>\n          </Popconfirm>\n        </Flex>\n      </Flex>\n      <Flex justify=\"center\" align=\"center\" style={{ whiteSpace: 'nowrap' }}>\n        <Popconfirm\n          placement=\"bottomLeft\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>BL</Button>\n        </Popconfirm>\n        <Popconfirm\n          placement=\"bottom\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>Bottom</Button>\n        </Popconfirm>\n        <Popconfirm\n          placement=\"bottomRight\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>BR</Button>\n        </Popconfirm>\n      </Flex>\n    </Flex>\n  </ConfigProvider>\n);\n\nexport default App;\n";;
  